import React, {useEffect, useState} from 'react';
import {useObserver} from 'mobx-react';
import {Chip, Dialog, DialogContent, Fab, Fade, Grid, Icon, Theme, Tooltip} from "@material-ui/core";
import ContentLayout from "../../components/commons/ContentLayout";
import {FirebaseServices, Workbook} from "../../services";
import {IDocUser} from "../../constants/interfaces";
import MaterialTable, {MTableToolbar} from "material-table";
import LoadingBackground from "../../components/commons/LoadingBackdrop";
import {makeStyles} from "@material-ui/core/styles";
import DetailUser from "./DetailUser";
import {TransitionProps} from "@material-ui/core/transitions";

const Transition = React.forwardRef<unknown, TransitionProps>(function Transition(props, ref) {
    return <Fade
        ref={ref}
        {...props}
    />;
});

const useStyles = makeStyles((theme: Theme) => ({
    chips: {
        margin: '8px',
        color: 'white',
    }
}));

const ListUser: React.FC = () => {
    const classes = useStyles();
    const [users, setUsers] = useState<IDocUser[]>([]);
    const [loading, setLoading] = useState<boolean>(false);
    const [userCountChip, setUserCountChip] = useState<string>('Registered : 0');
    const [userFillingUp, setUserFillingUp] = useState<string>('Filling up : 0');
    const [userSubmitted, setUserSubmitted] = useState<string>('Submitted : 0');
    const [lastOpenNumber, setLastOpenNumber] = useState<number>(0);
    const [openDialog, setOpenDialog] = useState<boolean>(false);
    const [selectedDocUser, setSelectedDocUser] = useState<IDocUser | null>(null);

    const handleClose = () => {
        setOpenDialog(false);
        setSelectedDocUser(null);
    };

    useEffect(() => {
        const init = async () => {
            setLoading(true);
            const resultUsers = await FirebaseServices.getInstance().fetchUsersWithApplicationForm();
            setUsers(resultUsers);
            setUserCountChip(`Registered : ${resultUsers.length}`);

            const fillingUpCount = (resultUsers.filter(data => data.countApplicationForm > 0 && !data?.applicationForm?.configData?.isSubmitted)).length;
            setUserFillingUp(`Filling up : ${fillingUpCount}`);

            const submittedCount = (resultUsers.filter(data => data.applicationForm?.configData.isSubmitted)).length;
            setUserSubmitted(`Submitted : ${submittedCount}`);
            setLoading(false);
        };

        init();
    }, []);

    return useObserver(() => (<>
        <ContentLayout>
            <LoadingBackground open={loading}/>
            <Grid container>
                <Grid item xs={12}>
                    <div style={{maxWidth: "100%"}}>
                        <MaterialTable
                            title={'Users'}
                            columns={[
                                {title: 'No.', field: 'no'},
                                {title: 'Email', field: 'email'},
                                {title: 'Role', field: 'role'},
                                {
                                    title: 'Information',
                                    render: (rowData) => {
                                        let colorApplicationForm: any = 'disabled';
                                        if ((rowData as IDocUser)?.countApplicationForm > 0) colorApplicationForm = "secondary";
                                        if ((rowData as IDocUser)?.applicationForm?.configData?.isSubmitted) colorApplicationForm = "primary";

                                        return (
                                            <>
                                                <Tooltip title="Data submitted" aria-label="last-open-data">
                                                    <Icon fontSize={"small"}
                                                          color={colorApplicationForm}>check_circle</Icon>
                                                </Tooltip>
                                                <Tooltip title="Your last open data" aria-label="last-open-data">
                                                    <Icon fontSize={"small"}
                                                          color={lastOpenNumber === rowData.no ? "secondary" : "disabled"}>info</Icon>
                                                </Tooltip>
                                            </>
                                        );
                                    },
                                    customSort: (a, b) => (a as IDocUser).countApplicationForm - (b as IDocUser).countApplicationForm,
                                }
                            ]}
                            options={{
                                grouping: true,
                                actionsColumnIndex: -1,
                                pageSizeOptions: [50, 100, 150, 200],
                                pageSize: 50,
                                exportButton: true,
                                exportCsv: (async (columns, renderData) => {
                                    await Workbook.getInstance().createAndSave(renderData);
                                }),
                            }}
                            data={users}
                            actions={[
                                {
                                    icon: 'open_in_browser',
                                    tooltip: 'Open modal applicant data',
                                    onClick: (event, rowData) => {
                                        setLastOpenNumber((rowData as IDocUser).no ?? 0);
                                        setOpenDialog(true);
                                        setSelectedDocUser((rowData as IDocUser));
                                        // window.open(`${CURRENT_ROUTE}${DETAIL_USER}${(rowData as IDocUser).uid}`, '_blank');
                                    },
                                },
                                // {
                                //     icon: 'open_in_new',
                                //     tooltip: 'Open applicant data on new tab',
                                //     onClick: (event, rowData) => {
                                //         setLastOpenNumber((rowData as IDocUser).no ?? 0);
                                //         window.open(`${CURRENT_ROUTE}${DETAIL_USER}${(rowData as IDocUser).uid}`, '_blank');
                                //     },
                                // },
                            ]}
                            components={{
                                Toolbar: props => (
                                    <>
                                        <MTableToolbar {...props} />
                                        <div style={{padding: '0px 10px'}}>
                                            <Chip label={userCountChip} style={{color: 'black'}}
                                                  className={classes.chips}/>
                                            <Chip label={userFillingUp} color={'secondary'} className={classes.chips}/>
                                            <Chip label={userSubmitted} color={'primary'} className={classes.chips}/>
                                        </div>
                                    </>
                                )
                            }}
                        />
                    </div>
                </Grid>
            </Grid>
        </ContentLayout>

        <Dialog
            open={openDialog}
            onClose={handleClose}
            aria-labelledby="user-dialog-title"
            aria-describedby="user-dialog-description"
            fullScreen={true}
            scroll={"body"}
            TransitionComponent={Transition}
        >
            <DialogContent>
                <div style={{width: "max-content", margin: "auto", marginTop: 75}}>
                    <Fab aria-label="close" onClick={handleClose}>
                        <Icon>close</Icon>
                    </Fab>
                </div>
                <DetailUser
                    uidProps={selectedDocUser?.uid}
                    hiddenTitle={true}
                />
            </DialogContent>
        </Dialog>
    </>));
};

export default ListUser;
